<template>
  <div>
    <div v-if="rightCn > 0" style="text-align: left; color: red">
      <font-awesome-icon
        v-for="i in rightCn"
        :key="i"
        icon="star"
        fixed-width
      />
      {{ rightCn }}
    </div>
    <div v-if="wrongCn > 0" style="text-align: left; color: gray">
      <font-awesome-icon
        v-for="i in wrongCn"
        :key="i"
        icon="star"
        fixed-width
      />{{ wrongCn }}
    </div>

    <ul class="figure-list">
      <li v-for="(item, k) in list" :key="k">
        <div style="text-align: left">{{ item.cnText }}-{{ item.enText }}</div>
        <figure
          class="animated"
          style="cursor: pointer"
          @click="
            select(k, item);
            clickIndex = k;
          "
          :style="{
            backgroundImage: `url(/api2/proxy?url=${encodeURIComponent(
              item.imgUrl
            )}&enText=${encodeURIComponent(item.enText.trim())})`,
          }"
        ></figure>
      </li>
    </ul>
    <div @click="replay" style="cursor: pointer">
      <font-awesome-icon
        :class="{ speaking: speaking }"
        icon="volume-up"
        fixed-width
      />Replay 重播({{ seconds }}s)
      <audio ref="audio" v-show="false" style="height: 0px; width: 0px"></audio>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
window.$ = $;

export default {
  data: () => {
    return {
      rightCn: 0,
      wrongCn: 0,
      curAct: 0,
      list: [],
      curIndex: 0,
      speaking: false,
      seconds: 15,
      timer: 0,
    };
  },
  computed: {},
  mounted: function () {
    this.randList();
    this.resetClock();
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },

  methods: {
    replay() {
      var item = this.list[this.curIndex];
      this.say("which is " + item.enText);
    },
    getIndicator(who) {
      return "";
    },
    select(index, item) {
      this.resetClock(true);
      (async () => {
        if (index == this.curIndex) {
          await this.say(item.enText, "en")
            .then(() => this.say(this.getRightSetence(), "en"))
            .then(() => this.randList());

          this.rightCn++;
          this.curAct = 1;
        } else {
          await this.say(item.enText, "en")
            // .then(() => this.say(item.cnText, "zh"))
            .then(() => this.say(this.getWrongSetence(), "en"));
          this.wrongCn++;
          this.curAct = -1;
        }
        this.resetClock();
      })();
    },
    resetClock(onlyClear) {
      if (onlyClear) clearInterval(this.timer);
      else {
        if (this.timer) clearInterval(this.timer);
        this.timer = setInterval(() => {
          this.seconds--;
          if (this.seconds == 0) {
            this.replay();
            this.seconds = 15;
          }
        }, 1000);
      }
    },
    randList() {
      let _this = this;
      $.getJSON("/api2/randList", { n: 4 }).then((data) => {
        _this.list.length = 0;
        _this.list.push(...data);
        _this.curIndex = parseInt(Math.random() * 4);
        _this.replay();
      });
    },
    say(str, whovoice = "en") {
      this.speaking = true;
      if (whovoice == "en") whovoice = " -v Samantha ";
      else if (whovoice == "zh") whovoice = " -v Ting-Ting ";
      let usingLocal = true;
      if (usingLocal)
        return $.get("/api2/say", { str: whovoice + str }).then(() => {
          this.speaking = false;
        });
      else
        return $.get("/api2/tts", { lan: "en", str: str }).then((data) => {
          return new Promise((resolve, reject) => {
            if (data.URL) {
              this.$refs.audio.src = data.URL;

              this.$refs.audio.addEventListener("ended", function () {
                resolve();
                this.speaking = false;
              });
              this.$refs.audio.play();
            }
          });
        });
    },
    getWrongSetence() {
      let list = ["no no no", "oh sorry.", "incorrect", "come on."];
      let i = parseInt(Math.random() * list.length);
      return list[i];
    },
    getRightSetence() {
      let list = ["yes yes yes", "well done", "good job"];
      let i = parseInt(Math.random() * list.length);
      return list[i];
    },
  },
  watch: {},
};
</script>

